.ReactTable {
  .rt-thead.-header {
    border-bottom: 1px solid $gray-300;
    box-shadow: none;
  }
  .rt-thead .rt-th, .rt-thead .rt-td {
    padding-top: .5rem;
    padding-bottom: .5rem;
  }
  .rt-tr.-even {
    background-color: mix($gray-100, white, 70);
  }
  .-pagination {
    box-shadow: none;
    border-top-width: 1px;
  }
}


