.nav {
  &--header {
    border-bottom: 1px solid $border-color;
    // box-shadow: $box-shadow-sm;
    background-color: $white;
    align-items: center;
    .nav__account {
      // position: absolute;
      // right: $grid-gutter-width;
      // top: 50%;
      // transform: translateY(-50%);
      margin-left: auto;
      text-align: right;
      padding-right: $grid-gutter-width * .5;
      &__item {
        display: inline-block;
      }
      a {
        color: $gray-700;
      }
      &__tool-toggle {
        display: none;
      }
      @include cust-breakpoint(pad) {
        // display: none;
        &__items {
          display: flex;
          flex-direction: column;
          position: absolute;
          right: ($grid-gutter-width * .5);
          top: $header-nav-height;
          background-color: $white;
          padding: $grid-gutter-width ($grid-gutter-width * .5);
          margin-right: $logout-info-width * -1 - $grid-gutter-width;
          box-shadow: $box-shadow-sm;
          border-radius: $border-radius;
          text-align: left;
          width: $logout-info-width;
          transition: margin .25s ease;
          &::before {
            content: '';
            width: 0;
            height: 0;
            border-style: solid;
            border-width: 0 6px 10px 6px;
            border-color: transparent transparent $white transparent;
            position: absolute;
            top: ($grid-gutter-width * -.5);
            right: ($grid-gutter-width * .75);
          }
          .nav__account__item {
            display: block;
            padding: $input-btn-padding-y $input-btn-padding-x;
            &:last-child {
              text-align: right;
            }
          }
          &--hide {
            margin-right: $logout-info-width * -1 - $grid-gutter-width;
          }
          &--show {
            margin-right: 0;
          }
        }
        &__tool-toggle {
          display: inline-block;
          margin-left: 0;
          font-size: $font-size-lg;
        }
      }
    }
    .navbar-brand {
      padding: ($grid-gutter-width * .25) 0 ($grid-gutter-width * .5) ($grid-gutter-width * .5);
      margin-right: 0;
      width: $sidebar-width;
    }
    .navbar-toggler {
      height: $navbar-toggler-height;
      border-color: mix($main-color, $gray-100);
      i {
        color: mix($main-color, $gray-100, 70);
      }
    }
  }
}


