// pure layout setting
.layout { 
  &__main .row {
    height: 100%;
  }
  &--default .layout__main {
    position: absolute;
    left: 0;
    right: 0;
    top: $header-nav-height;
    bottom: 0;
    > .row {
      flex-direction: row;
    }
  }
}
.nav--header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: $header-nav-height;
  z-index: index($list: $page-index, $value: nav);
}
// layout structure
.sidebar--nav {
  position: fixed;
  z-index: index($list: $page-index, $value: sidebar);
  width: $sidebar-width;
  top: $header-nav-height;
  left: 0;
  bottom: 0;
  overflow: auto;
  transition: margin .5s ease;
  background-color: $white;
}
.main {
  display: block;
  overflow: auto;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin-left: $sidebar-width;
  z-index: index($list: $page-index, $value: main);
  background-color: $gray-200;
  transition: margin .5s ease;
  &__content {

  }
}
// toggle structure
.sidebar {
  &--open {
    .main {
      margin-left: 0;
    }
    @include cust-breakpoint(lg) {
      .main {
        margin-left: $sidebar-width;
      }
    }
  }
  &--close {
    .main {
      margin-left: 0;
    }
    .sidebar--nav {
      margin-left: $sidebar-width * -1;
    }
  }
}

// boxes
.content-box {
  background-color: white;
  + .content-box {
    margin-top: $grid-gutter-width;
  }
}
.wrap-box {
  &--s {
    padding: $grid-gutter-width * .5;
  }
  &--m {
    padding: $grid-gutter-width * 1;
  }
  &--l {
    padding: $grid-gutter-width * 1.5;
  }
  &--l {
    padding: $grid-gutter-width * 2;
  }
}
.wrap-left {
  &--s {
    padding-left: $grid-gutter-width * .5;
  }
  &--m {
    padding-left: $grid-gutter-width * 1;
  }
  &--l {
    padding-left: $grid-gutter-width * 1.5;
  }
  &--l {
    padding-left: $grid-gutter-width * 2;
  }
}
.wrap-right {
  &--s {
    padding-right: $grid-gutter-width * .5;
  }
  &--m {
    padding-right: $grid-gutter-width * 1;
  }
  &--l {
    padding-right: $grid-gutter-width * 1.5;
  }
  &--l {
    padding-right: $grid-gutter-width * 2;
  }
}
.wrap-top {
  &--s {
    padding-top: $grid-gutter-width * .5;
  }
  &--m {
    padding-top: $grid-gutter-width * 1;
  }
  &--l {
    padding-top: $grid-gutter-width * 1.5;
  }
  &--l {
    padding-top: $grid-gutter-width * 2;
  }
}
.wrap-bottom {
  &--s {
    padding-bottom: $grid-gutter-width * .5;
  }
  &--m {
    padding-bottom: $grid-gutter-width * 1;
  }
  &--l {
    padding-bottom: $grid-gutter-width * 1.5;
  }
  &--l {
    padding-bottom: $grid-gutter-width * 2;
  }
}


