.sidebar {
  &--nav {
    box-shadow: $box-shadow-sm;
    .sidebar__list {
      // all ul
      ul {
        list-style: none;
        padding-left: 0;
        margin-bottom: 0;
      }
      // all a
      a {
        padding: .75rem $grid-gutter-width;
        display: block;
        color: $gray-600;
        transform: translateY(0);
        -webkit-font-smoothing: antialiased;
        &:hover {
          background-color: mix($gray-100, $gray-200);
          text-decoration: none;
        }
        > i {
          margin-right: $grid-gutter-width * .5;
        }
        &.sidebar__item--active {
          background-color: rgba($main-color, .7);
          color: $white;
          font-weight: bold;
        }
        @extend %ripple;
      }
      // first layer ul
      > ul {
        margin: $grid-gutter-width 0;
        > li > div > ul > li > a {
          padding-left: $grid-gutter-width * 2.75;
        }
      }
    }
    .sidebar__item {
      &--collapse > a, &--collapsed > a {
        position: relative;
        &::before {
          content: $ionicon-var-ios-arrow-left;
          @extend .ion;
          position: absolute;
          right: $grid-gutter-width;
          top: 50%;
          transform: translateY(-50%);
        }
      }
      &--collapsed > a {
        font-weight: bold;
        &::before {
          content: $ionicon-var-ios-arrow-down;
        }
      }
    }
  }
}


