.login {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: index($list: $page-index, $value: login);
  overflow: auto;
  text-align: center;
  &:before {
    content: '';
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    width: 1px;
    margin-left: -1px;
  }
  &__inner {
    display: inline-block;
    vertical-align: middle;
    text-align: left;
    width: $login-width;
    max-width: 100%;
    margin: ($login-logo-height + $grid-gutter-width * 2) 0 $grid-gutter-width;
    position: relative;
    @extend .wrap-box--m;
    box-shadow: $box-shadow;
    border-radius: $border-radius;
    z-index: index($list: $page-index, $value: login);
    background-color: $white;    
  }
  &__logo {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    margin-top: ($login-logo-height + $grid-gutter-width) * -1;
  }
  &__title {
    font-size: $h4-font-size;
    margin-bottom: $grid-gutter-width;
  }
  &__body {}
  &__backdrop {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: index($list: $page-index, $value: loginBackdrop);
    background-color: $gray-200;
  }
}


